import React, { useReducer, useEffect, useState } from 'react'
import { dropLast, head, last, keys, map, range } from "ramda"

import './App.css'
import {
    generateInitSteps,
    generateNextStep,
    newStep,
    playerActionType,
    playStage,
    stepColors,
    stepType
} from "./stepLogic"
import { isSomething } from "./utils"
import Login, { VALID_TOKEN } from "./Login"


const initialState = {
    steps: generateInitSteps(),
    playedSteps: [],
    slots: [],
    prevSlots: [],
    lastPlayerActionTypePlayed: playerActionType.WAIT,
    resource: 0,
    slotMaximum: 3
}

const WIN_SLOT_COUNT = 5

function App() {
    // const [ logs, addToLog ] = useReducer((state, newItem) => [ newLog(newItem), ...state ], [])
    const [ {
        steps, playedSteps, slots, prevSlots, lastPlayerActionTypePlayed, resource,
        slotMaximum
    }, dispatch ] = useReducer(
        (state, { type }) => {
            const firstStep = head(state.steps)
            const lastStep = last(state.steps)
            const hasUpgrade =
                state.slots.length === state.slotMaximum &&
                state.resource >= state.slotMaximum

            const hasBossUpgradeA = state.playedSteps.filter(step => step.type === stepType.A).length >= 5 &&
                lastStep.type === stepType.A
            const hasBossUpgradeB = state.playedSteps.filter(step => step.type === stepType.B).length >= 5 &&
                lastStep.type === stepType.B
            const hasBossUpgradeC = state.playedSteps.filter(step => step.type === stepType.C).length >= 5 &&
                lastStep.type === stepType.C
            const hasBossUpgradeD = state.playedSteps.filter(step => step.type === stepType.D).length >= 5

            const addedToSlots = [ lastStep, ...state.slots.slice(0, state.slotMaximum - 1) ]
            const doubleAddedToSlots =
                [ lastStep, { ...lastStep, id: (lastStep.id + 1000) * -1 }, ...state.slots.slice(0, state.slotMaximum - 2) ]

            switch (type) {
                case 'onHoldClicked':

                    return {
                        ...state,
                        lastPlayerActionTypePlayed: playerActionType.HOLD,
                        slots: hasBossUpgradeC ? doubleAddedToSlots : addedToSlots,
                        prevSlots: state.slots,
                        steps: [ newStep(generateNextStep(firstStep.type)), ...dropLast(1, state.steps) ],
                        playedSteps: [ ...state.playedSteps, lastStep ]
                    }

                case 'onInvestClicked':

                    return {
                        ...state,
                        lastPlayerActionTypePlayed: playerActionType.INVEST,
                        slots:
                            hasBossUpgradeD
                                ? hasBossUpgradeB
                                    // TODO: узкое место. Что если камни остаются, то ветер протиснется всё равно?
                                    ? [ lastStep, ...state.slots.filter(step => step.type === stepType.D) ]
                                    : state.slots.filter(step => step.type === stepType.D)
                                : hasBossUpgradeB ? [ lastStep ] : [],
                        // TODO: перенести в способности
                        // slots: lastStep.type === stepType.C
                        //     ? append(lastStep, state.slots.filter(slot => slot.type === stepType.C))
                        //     : state.slots.filter(slot => slot.type === stepType.C),
                        prevSlots: state.slots,
                        steps: [ newStep(generateNextStep(firstStep.type)), ...dropLast(1, state.steps) ],
                        playedSteps: [ ...state.playedSteps, lastStep ],

                        // hasUpgrade
                        slotMaximum: hasUpgrade
                            ? Math.min(state.slotMaximum + 1, WIN_SLOT_COUNT)
                            : state.slotMaximum,
                        resource: hasUpgrade ? state.resource - state.slotMaximum : state.resource
                    }

                case 'onWaitClicked':

                    return {
                        ...state,
                        lastPlayerActionTypePlayed: playerActionType.WAIT,
                        steps: [ newStep(generateNextStep(firstStep.type)), ...dropLast(1, state.steps) ],
                        slots: hasBossUpgradeB ? addedToSlots : state.slots,
                        // TODO: перенести в способности
                        // slots: lastStep.type === stepType.C
                        //     ? [ lastStep, ...state.slots.slice(0, SLOT_MAXIMUM - 1) ]
                        //     : state.slots,
                        prevSlots: state.slots,
                        playedSteps: [ ...state.playedSteps, lastStep ],
                        resource: hasBossUpgradeA ? state.resource : state.resource + 1
                    }

                default:
                    return state
            }
        }, initialState)
    const [ token, setToken ] = useState('')
    const isLogged = token === VALID_TOKEN

    const stage = selectStage()

    const onHoldClick = () => {
        dispatch({ type: 'onHoldClicked' })
    }
    const onInvestClick = () => {
        dispatch({ type: 'onInvestClicked' })
    }
    const onWaitClick = () => {
        dispatch({ type: 'onWaitClicked' })
    }

    useEffect(() => {
        isLogged && document.addEventListener('keypress', (e) => {
            switch (e.key) {
                case '1':
                    onHoldClick()
                    break
                case '2':
                    onInvestClick()
                    break
                case '3':
                    onWaitClick()
                    break
                default:
                    break
            }
        })
    }, [ isLogged ])

    const playedStepsInfo = playedSteps.reduce((acc, step) => {

        return { ...acc, [step.type]: acc[step.type] + 1 }
    }, {
        [stepType.A]: 0,
        [stepType.B]: 0,
        [stepType.C]: 0,
        [stepType.D]: 0
    })

    function renderSlots() {
        const slotsWithEmpties = [ ...slots, ...map(n => ({ id: n, slotColor: 'white' }), range(-5, 0)) ]
            .slice(0, slotMaximum)

        return slotsWithEmpties.map(slot => (
            <div
                key={ slot.id }
                className="slot"
                style={ { background: slot.slotColor } }
            />
        ))
    }

    if (!isLogged)
        return (
            <Login setToken={ setToken }/>
        )

    return (
        <main>
            <div>
                <div className="steps">
                    { steps.map(step => (
                        <div
                            key={ step.id }
                            className="step"
                            style={ { background: step.stepColor } }
                        >
                            { keys(step.items).map(itemId => (
                                <div key={ itemId }
                                     className="step-item">{ isSomething(step.items[itemId]) && step.items[itemId] }</div>
                            )) }
                        </div>
                    )) }
                </div>
                { stage === playStage.IN_PLAY && (
                    <div className="action-panel">
                        <button className="action" onClick={ onHoldClick }>Захватить</button>
                        <button className="action" onClick={ onInvestClick }>Вложиться</button>
                        <button className="action" onClick={ onWaitClick }>Ждать</button>
                    </div>
                ) }
                { stage === playStage.WIN && 'ПОБЕДА!' }
                { stage === playStage.LOSE && 'ПОРАЖЕНИЕ!' }
                <div className="slot-panel">
                    { renderSlots() }
                </div>
                <div
                    className="played-steps"
                >
                    <div
                        className="played-step played-step_a"
                        style={ { background: stepColors[stepType.A] } }
                    >
                        { playedStepsInfo[stepType.A] >= 5 ? 'Теперь ЖДАТЬ не приносит ресурс' : playedStepsInfo[stepType.A] }
                    </div>
                    <div
                        className="played-step played-step_b"
                        style={ { background: stepColors[stepType.B] } }
                    >
                        { playedStepsInfo[stepType.B] >= 5 ? 'Теперь СИНИЙ шаг всегда крепится' : playedStepsInfo[stepType.B] }
                    </div>
                    <div
                        className="played-step played-step_c"
                        style={ { background: stepColors[stepType.C] } }
                    >
                        { playedStepsInfo[stepType.C] >= 5 ? 'Теперь КРАСНЫЙ шаг крепится дважды' : playedStepsInfo[stepType.C] }
                    </div>
                    <div
                        className="played-step played-step_d"
                        style={ { background: stepColors[stepType.D] } }
                    >
                        { playedStepsInfo[stepType.D] >= 5 ? 'Теперь ЖЁЛТЫЙ шаг нельзя сбросить' : playedStepsInfo[stepType.D] }
                    </div>
                </div>
                {/*<div className="step step_last"></div>*/ }
                {/*<div className="logs">*/ }
                {/*    { logs.map(log => (*/ }
                {/*        <div key={ log.id }>{ log.text }</div>*/ }
                {/*    )) }*/ }
                {/*</div>*/ }
            </div>
            <div>
                <div className="player-info">{ resource }</div>
            </div>
        </main>
    )

    function selectStage() {
        if (
            prevSlots.length >= WIN_SLOT_COUNT &&
            lastPlayerActionTypePlayed === playerActionType.INVEST
        )
            return playStage.WIN

        if (slots.length === slotMaximum && slots.every(slot => slot.type === slots[0].type))
            return playStage.LOSE

        return playStage.IN_PLAY
    }
}


/*<div className="step step_next"></div>*/


export default App


