import React, { useEffect, useRef } from 'react'
import { path } from 'ramda'


const KEY = 'user'
export const VALID_TOKEN = 'vr"(Jwb+Gu.aev7V'

export default function Login({ setToken }){
    useEffect(() => {
        const token = localStorage.getItem(KEY)

        setToken(token)
    })

    const login = useRef(null)
    const password = useRef(null)

    const onClick = () => {
        const loginValue = path([ 'current', 'value' ], login)
        const passwordValue = path([ 'current', 'value' ], password)

        if (loginValue === 'proto' && passwordValue === 'taunt1') {
            localStorage.setItem(KEY, VALID_TOKEN)
            setToken(VALID_TOKEN)
        }
    }

    return (
        <form onSubmit={ onClick }>
            <input ref={ login }/>
            <input ref={ password } type="password"/>
            <button type="submit">Войти</button>
        </form>
    )
}