import { head, prop } from "ramda"


export const playStage = {
    IN_PLAY: 'IN_PLAY',
    WIN: 'WIN',
    LOSE: 'LOSE'
}

export const stepType = {
    A: 'A',
    B: 'B',
    C: 'C',
    D: 'D'
}

export const playerActionType = {
    HOLD: 'HOLD',
    INVEST: 'INVEST',
    WAIT: 'WAIT'
}

const order = new Map([
    // Другие, кроме себя
    [ stepType.A, [
        stepType.B,
        stepType.C,
        stepType.D
    ] ],
    // Все, а тажке D
    [ stepType.B, [
        stepType.A,
        stepType.B,
        stepType.C,
        stepType.D
    ] ],
    [ stepType.C, [
        stepType.A,
        stepType.C
    ] ],
    [ stepType.D, [
        stepType.C
    ] ]
])

export const generateNextStep = (prevStepType) => {
    const possibleSteps = order.get(prevStepType)

    const count = possibleSteps.length

    return possibleSteps[Math.floor(Math.random() * count)]
}

export const stepColors = {
    [stepType.A]: '#6ccc6c',
    [stepType.B]: '#9292ff',
    [stepType.C]: '#ff8c8c',
    [stepType.D]: '#faff7e'
}

const generateBool = () => {
    const values = [ true, false ]

    return values[Math.floor(Math.random() * values.length)]
}


let stepId = 0

export function newStep(stepType) {
    const isHidden = generateBool()

    return {
        id: stepId++,
        type: stepType,
        get stepColor() {
            if (isHidden)
                return 'white'

            return stepColors[stepType]
        },
        get slotColor() {

            return stepColors[stepType]
        },
        isHidden
    }
}


const STEP_LIMIT = 3
export const generateInitSteps = (count = STEP_LIMIT, prevResult = [ newStep(stepType.A) ]) => {
    if (count < 2)
        return prevResult

    return generateInitSteps(count - 1,
        [ newStep(generateNextStep(prop('type', head(prevResult)))), ...prevResult ]
    )
}